::-webkit-scrollbar{
    width: 20px;
}

::-webkit-scrollbar-track{
    background: transparent;
}

::-webkit-scrollbar-thumb{
    background: tomato;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
}

*{
    box-sizing: border-box;
    scrollbar-color: tomato bisque;
    scrollbar-width: thin;
}

body{
    margin: 0;
}


#main{
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-color: bisque;
    font-size: 20px;
}

.topper{
    z-index: 99;
    box-shadow: 0px 5px 8px 2px rgba(255, 99, 71, 0.63);
}

.rest::before{
    content: "";
    background-image: url('./images/homebg.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    top: 5em;
    right: 0px;
    bottom: 0px;
    left: 0px;
    opacity: .4;
    background-position: 50%0%;
}

.rest{
    display: flex;
    width: 100%;
    overflow: hidden;
}

